import BetOrder from '../models/BetOrder'
const OpenBet = BetOrder.OpenBet
import express from 'express';
const router = express.Router();

import moment from 'moment'

router.get('/fetch-open-bets', (req, res) => {
	OpenBet.find({user: req.user.username}, (err, result) => {
		if(err) res.status(404).send('fetch user open bet error')
		res.json(result)
	})
})

export default router;

